import React, { Component } from "react";
import { withRouter, Redirect } from 'react-router-dom';
import { withStyles } from "@material-ui/core/styles";
import { register } from "./Styles/RegistrationStyles";

import CssBaseline from "@material-ui/core/CssBaseline";
import Paper from "@material-ui/core/Paper";
import Avatar from "@material-ui/core/Avatar";
import Snackbar from "@material-ui/core/Snackbar";
import SnackbarContent from "@material-ui/core/SnackbarContent";
import IconButton from "@material-ui/core/IconButton";
import ErrorIcon from "@material-ui/icons/Error";
import CloseIcon from "@material-ui/icons/Close";
import logo from "../../../resources/images/logo.jpg";
import { Grid } from "@material-ui/core";
import RegistrationConfirmation from "./Forms/RegistrationConfirmation";
import axios from "axios";
import Cookies from "universal-cookie";
require('dotenv').config()

class RegistrationConfirm extends Component {
  state = {
    otppassword: "",
    email: "",
    password: "",
    passwordConfirm: "",
    hidePassword: true,
    error: null,
    errorOpen: false,
    showSignUpChoices: false,
    signUpChoices: {customer: false, company: false, admin: false},
    firstname: "",
    lastname: "",
    birthdate: "",
    name: "",
    siret: "",
    documents: "",
    street: "",
    postalCode: "",
    city: "",
    region: "",
    country: "",
    jwt: ""
  };

  componentDidMount() {
    this.setState({
      email: this.props.location.state.email,
      password: this.props.location.state.password,
      passwordConfirm: this.props.location.state.passwordConfirm,
      jwt: this.props.location.state.jwt
    });
    console.log(this.props.location.state);
    console.log(process.env.TEXT);
  }

  errorClose = e => {
    this.setState({
      errorOpen: false
    });
  };

  handleChange = name => e => {
    this.setState({
      [name]: e.target.value
    });
  };

  isValid = () => {
    if (this.state.email === "" || this.state.password === "" || this.state.passwordConfirm === "") {
      return false;
    }
    return true;
  };

  isOtpPasswordValid = () => {
    if (/^([0-9]{6})$/.test(this.state.otppassword)) {
      return true;
    }
    return false;
  }

  completeRegistration = async (e) => {
    e.preventDefault();
    if (this.isOtpPasswordValid()){
      this.setState({
        error: null
      });

      const response = await axios({
        method: 'POST',
        url: 'http://localhost:8080/api/auth/validate',
        headers: {
          "Authorization": "Bearer "+this.state.jwt
        },
        data: {
          code: this.state.otppassword
        }
      });
      if(response.data.auth){
        console.log(response.data);
        let cookies = new Cookies();//2592000
        cookies.set('access_token', response.data.access_token, { path: '/' });
        cookies.set('refresh_token', response.data.refresh_token, { path: '/' });
        console.log(cookies.get('access_token'));
        console.log(cookies.get('refresh_token'));
      }
    }else{
      this.setState({
        errorOpen: true,
        error: "Incorrect code format"
      });
    }
  }

  render() {
    const { classes } = this.props;
    return (
        <Grid item>
          <div className={classes.main}>
            <CssBaseline />

            <Paper className={`${classes.paper}`}>
              {<Avatar className={classes.avatar}>
                <img className={classes.logo} src={logo} alt="Logo" />
              </Avatar>}
              <form
                  className={classes.form}
                  onSubmit={() => this.submitRegistration}
              >
                <RegistrationConfirmation registration={this}/>
              </form>
              {this.state.error ? (
                  <Snackbar
                      variant="error"
                      key={this.state.error}
                      anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "center"
                      }}
                      open={this.state.errorOpen}
                      onClose={this.errorClose}
                      autoHideDuration={3000}
                  >
                    <SnackbarContent
                        className={classes.error}
                        message={
                          <div>
                    <span style={{ marginRight: "8px" }}>
                      <ErrorIcon fontSize="large" color="error" />
                    </span>
                            <span> {this.state.error} </span>
                          </div>
                        }
                        action={[
                          <IconButton
                              key="close"
                              aria-label="close"
                              onClick={this.errorClose}
                          >
                            <CloseIcon color="error" />
                          </IconButton>
                        ]}
                    />
                  </Snackbar>
              ) : null}
            </Paper>
          </div>
        </Grid>
    );
  }
}

export default withRouter(withStyles(register)(RegistrationConfirm));